#about {
    background-image: linear-gradient(to right bottom, #37084d, #250d41, #1a103c, #17133f, #0e1339, #0b1336, #091233, #071130, #071130, #071130, #071130, #071130);
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    color: #fff;
    text-align: center;
}

.blink {
    animation: blink .875s 1s infinite;
    border-right: 3px solid #fff;
    padding-right: 0.5rem;
}

.about__sticky {
    width: 40%;
}

.code-nav {
    display: flex;
    margin-bottom: 2rem;
}

.red-circle,
.green-circle,
.orange-circle {
    background-color: rgb(255, 21, 0);
    border-radius: 100%;
    width: 0.75rem;
    height: 0.75rem;
    margin-right: 0.5rem;
}

.orange-circle {
    background-color: rgb(255 159 36);
}

.green-circle {
    background-color: rgb(0, 255, 85);
}

.about__sticky .code__section {
    width: 90%;
    margin-top: 17.5%;
    margin-bottom: 15%;
    padding: 4% 10% 10% 5%;
    text-align: left;
    font-size: 1.25rem;
    border-radius: 0.6rem;
    border: 1px solid rgb(47, 50, 83);
    /*  border-image: linear-gradient(to right, #d80b77, #11152c) 1 0 0 0;*/
    background-image: linear-gradient(to right, #11152c, #0f132f, #0d1232, #0b0f35, #0a0d38);
    background-image: linear-gradient(to left top, #071130, #071130, #071130, #071130, #071130, #0a1131, #0c1031, #0f1032, #150f33, #1a0d33, #200b33, #250832);
}

.about__sticky span,
.about__sticky code,
.about__sticky .code__section {
    font-family: Consolas;
}

.about__sticky .code-margin {
    margin-left: 2.5rem;
}

.about__sticky .code-color-gray {
    color: rgb(165, 165, 165);
}

.about__sticky .code-color-cyan {
    color: cyan;
}

.about__sticky .code-color-purple {
    color: rgb(160, 0, 160);
    font-weight: bold;
}

.about__sticky .code-color-yellow {
    color: yellow;
}

.about__sticky .code-color-orange {
    color: rgb(255, 159, 36);
}

.about__content {
    text-align: justify;
    height: 75%;
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-self: center;
}

.about__content p {
    animation: fadeIn 1s ease-in;
    font-family: 'Cousine', monospace;
    font-variant: small-caps;
    font-size: 1.6rem;
}

@keyframes blink {
    0% {
        border-right-color: transparent;
    }

    100% {
        border-right-color: #fff;
    }
}