#technologies {
    overflow: hidden;
    height: 50vh;
    background: linear-gradient(to right, rgb(255, 255, 255), rgb(15, 15, 15), rgb(255, 255, 255));
    background: linear-gradient(to right, rgb(120, 24, 255), rgb(22, 0, 188), rgb(14, 7, 61));
    animation: gradient 6s linear infinite;
    background-size: 200%;
    display: grid;
    grid-gap: 1px;
    grid-template-columns: repeat(8, 12.5%);
    grid-template-rows: repeat(4, 25%);
    position: relative;
}

.TECHNOSECTION {
    margin: 0 12.5vw;
    width: 75vw;
    pointer-events: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    position: absolute;
    z-index: 50;
    top: 25%;
    transform: translateY(-20%);
}

.single__technologie {
    margin: 2rem 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc(100%/6);
}

.single__technologie img {
    object-fit: contain;
    object-position: center center;
    height: 90px;
    width: 90px;
    margin-bottom: 1.25rem;
    filter: grayscale(1);
    filter: grayscale(0);
    user-select: none;
    background-color: rgb(15, 15, 15);
}

.single__technologie p {
    color: #fff;
    font-size: 1.45rem;
    font-family: 'Cousine', monospace;
    font-family: "Source Sans Pro", sans-serif;
    font-family: 'Chakra Petch';
    background-color: rgb(15, 15, 15);
}

#technologies .square {
    overflow: hidden;
    user-select: none;
    transition: .125s ease-in-out;
    background-color: rgb(15, 15, 15);
}

#technologies .square:hover {
    background-color: rgb(20, 20, 20);
}

@keyframes gradient {
    0% {
        background-position: 0% 0%;
    }

    50% {
        background-position: 100% 100%;
    }

    100% {
        background-position: 0% 0%;
    }
}