#splash {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #040D25;
    z-index: 5;
    transition: transform 1s cubic-bezier(0.165, 0.84, 0.44, 1);
    overflow: hidden;
}

.remove-splash {
    transform: translateY(-100%);
}

#splash img {
    width: 90px;
}

#splash .arrow {
    color: #fff;
    font-size: 5rem;
    transform: translateY(42.5vh);
    animation: splashScreen 2.4s linear;
}

#splash .arrow.rotate {
    display: none;
}

@keyframes splashScreen {
    0% {
        transform: translateY(75.5vh);
    }

    33% {
        transform: translateY(42.5vh);
    }

    66% {
        transform: translateY(42.5vh);
    }

    100% {
        transform: translateY(-300vh);
    }
}