@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,400;0,700;1,400;1,700&family=Cousine:ital,wght@0,400;0,700;1,400;1,700&family=Lato:wght@400;700&display=swap');
:root {
    --g1: rgb(98, 0, 234);
    --g2: rgb(236, 64, 122);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: dimgrey;
    background-color: rgb(80, 80, 80);
    background-color: #b8b8b8;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: hidden;
}

a {
    text-decoration: none;
}

#splash {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #040D25;
    z-index: 5;
    transition: transform 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.remove-splash {
    transform: translateY(-100%);
}

#splash img {
    width: 90px;
}

.content__section {
    height: 100vh;
}


/*  PROJECTS SECTION  */

.projects__heading {
    user-select: none;
    text-align: center;
    width: 50%;
    font-family: 'Cousine', monospace;
    color: #fff;
    font-size: 3.75rem;
    font-weight: bold;
    margin: 7.5vh auto 2.5vh;
    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
    animation: glitch 700ms linear 5s infinite alternate;
}

.projects__heading::before {
    content: "";
    background-color: cyan;
    display: inline-block;
    height: 4px;
    position: relative;
    vertical-align: middle;
    width: 35%;
    margin-right: 1.75rem;
}

.projects__heading span {
    font-family: 'Chakra Petch', sans-serif;
    color: #ae00ff;
    font-size: 5rem;
    margin-left: 4px;
}

.projects__container {
    margin: 7.5vh auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 80%;
}

.project__box {
    width: 37.5%;
}

.project__description {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-top: 1.1rem;
    margin-bottom: 7.5vh;
}

.project__description p {
    margin-left: 0.25rem;
    text-transform: uppercase;
    font-size: 1.4rem;
    letter-spacing: 0.1px;
    text-align: center;
    font-family: 'Cousine', monospace;
    color: #ae00ff;
    user-select: none;
    text-align: left;
}

.project__description .row {
    background-color: cyan;
    height: 2px;
    position: relative;
    width: 45%;
    overflow: hidden;
}

.project__description .icon svg {
    font-size: 1.5rem;
    color: #fff;
    display: flex;
}

.project__card {
    width: 100%;
    height: 350px;
    border-radius: 12.5px;
    background-color: #303030;
    position: relative;
    overflow: hidden;
}

.project__card img {
    cursor: pointer;
    user-select: none;
    position: absolute;
    overflow: hidden;
    object-position: top;
    /* LUB center */
    object-fit: cover;
    bottom: 0;
    right: 50%;
    width: 100%;
    height: 100%;
    transform: translate(50%, 0%) rotate(0);
    transition: .3s cubic-bezier(0.39, 0.575, 0.565, 1);
    filter: grayscale(67.5%) blur(1.3px);
}

.project__card:hover img {
    transform: translate(50%, 0%) rotate(0deg) scale(1.1);
    filter: none;
}

.content__section:nth-of-type(1) {
    background-image: linear-gradient(to left bottom, #000562, #000b58, #000f4d, #000f42, #000f36, #040f34, #071031, #0a102f, #111136, #1a103c, #250d41, #37084d);
    position: relative;
    overflow: hidden;
}

#projects {
    height: auto;
    display: flex;
    flex-direction: column;
    background-color: #0e0e0e;
    padding-bottom: 7.5vh;
    background-image: linear-gradient(to left bottom, #091233, #0e112c, #111026, #120f1f, #110f19, #100f16, #100f13, #0f0f0f, #0f0f0f, #0f0f0f, #0e0e0e, #0e0e0e);
}

.content__section:nth-of-type(4) {
    background-color: #08092f;
    display: flex;
    justify-content: center;
    align-items: center;
}

#cursor {
    position: absolute;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: rgb(255, 0, 190);
    pointer-events: none;
    mix-blend-mode: difference;
    z-index: 100;
}


/* RZEŹBA - PARALAXA */

.sculpture {
    background-image: url('/public/photo2.png');
    height: 100vh;
    width: auto;
    background-position: top;
    background-size: auto;
    background-attachment: fixed;
    background-repeat: no-repeat;
    overflow: hidden;
    margin-left: 7.5%;
    animation: transitionFromBottom 2.1s ease-in;
}


/*  NAWIGACJA  */

.sections__nav {
    transform: rotate(90deg);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
    width: fit-content;
    width: 70px;
    display: none;
    position: absolute;
    top: 44.5%;
    opacity: 0;
    animation: fadeIn 0.15s ease-in-out forwards;
    color: #fff;
    margin-left: auto;
}

.sections__nav.nav__open {
    display: flex;
    opacity: 1;
}

.sections__nav a {
    color: #fff;
    list-style: none;
    font-family: 'Cousine', monospace;
    font-size: 1.5rem;
    cursor: pointer;
    transition: .215s;
    user-select: none;
    position: relative;
    align-items: center;
    display: flex;
    padding: 2.5% 15%;
    margin: 0 12.5px;
}

.sections__nav a:visited {
    color: #fff;
}

.sections__nav a::after {
    content: "";
    position: absolute;
    right: 8px;
    left: 8px;
    height: 4px;
    background: rgb(0, 255, 255);
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s cubic-bezier(0.4, 0.0, 0.2, 1);
}

.sections__nav a:hover::after {
    transform: scaleX(1);
    transform-origin: left;
}


/*  HAMBURGER MENU  */

.navigation {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    margin-right: 2.75rem;
}

label {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    width: 70px;
    cursor: pointer;
}

label span {
    background: #fff;
    border-radius: 10px;
    height: 7px;
    margin: 7px 0;
    transition: .4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
    animation: scaleIn 5s;
}

.navigation span:nth-of-type(1) {
    width: 50%;
}

.navigation span:nth-of-type(2) {
    width: 100%;
}

.navigation span:nth-of-type(3) {
    width: 75%;
}

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"]:checked~span:nth-of-type(1) {
    transform-origin: bottom;
    transform: rotatez(45deg) translate(8px, 0px)
}

input[type="checkbox"]:checked~span:nth-of-type(2) {
    transform-origin: top;
    transform: rotatez(-45deg)
}

input[type="checkbox"]:checked~span:nth-of-type(3) {
    transform-origin: bottom;
    width: 50%;
    transform: translate(30px, -11px) rotatez(45deg);
}

.name {
    font-family: 'Chakra Petch', sans-serif;
    font-size: 8.3rem;
    font-weight: bold;
    font-style: italic;
    color: #fff;
    position: absolute;
    top: 12%;
    left: 4.5%;
    user-select: none;
    opacity: 0;
    animation: fadeIn 0.75s ease-in-out forwards;
}

.name span {
    font-family: 'Chakra Petch', sans-serif;
    margin-left: 35%;
    opacity: 0;
    animation: fadeIn 2s ease-in-out forwards;
}

.job__title {
    font-size: 2.3rem;
    top: 67.5%;
    left: 5%;
    font-family: 'Cousine', monospace;
    color: #fff;
    position: absolute;
    font-weight: bold;
    font-style: italic;
    opacity: 0;
    animation: fadeIn 0.8s ease-in-out forwards;
}

.job__title .first__word {
    color: red;
    margin-right: 15%;
}

.job__title.visible {
    opacity: 1;
}

.name,
.name span:nth-child(1) {
    animation-delay: 0.15s;
}

.name,
.name span:nth-child(2) {
    animation-delay: 0.3s;
}

.name,
.name span:nth-child(3) {
    animation-delay: 0.45s;
}

.name,
.name span:nth-child(4) {
    animation-delay: 0.6s;
}

.name,
.name span:nth-child(5) {
    animation-delay: 0.75s;
}

.name,
.name span:nth-child(6) {
    animation-delay: 0.9s;
}


/* ANIMACJA POJAWIANIA SIĘ IMIENIA & NAZWISKA */

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes scaleIn {
    from {
        scale: 0;
    }
    to {
        scale: 1;
    }
}

@keyframes glitch {
    0% {
        text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
    }
    15% {
        text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
    }
    16% {
        text-shadow: -0.05em -0.025em 0 #00fffc, 0.025em 0.035em 0 #fc00ff, -0.05em -0.05em 0 #fffc00;
    }
    49% {
        text-shadow: -0.05em -0.025em 0 #00fffc, 0.025em 0.035em 0 #fc00ff, -0.05em -0.05em 0 #fffc00;
    }
    50% {
        text-shadow: 0.05em 0.035em 0 #00fffc, 0.03em 0 0 #fc00ff, 0 -0.04em 0 #fffc00;
    }
    99% {
        text-shadow: 0.05em 0.035em 0 #00fffc, 0.03em 0 0 #fc00ff, 0 -0.04em 0 #fffc00;
    }
    100% {
        text-shadow: -0.05em 0 0 #00fffc, -0.025em -0.04em 0 #fc00ff, -0.04em -0.025em 0 #fffc00;
    }
}

@keyframes transitionFromBottom {
    from {
        opacity: 0;
        transform: translateY(100%);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}


/* WAVE ANIMATION */

#contact {
    background-image: linear-gradient(to bottom, #0e0e0e, #0f0e15, #100f1b, #100f21, #0e1027, #0e112c, #0d1130, #0c1235, #111239, #17113c, #1e0f3f, #250d41);
}

.wave {
    background: rgb(255 255 255 / 25%);
    border-radius: 1000% 1000% 0 0;
    position: fixed;
    width: 200%;
    height: 12em;
    animation: wave 10s -3s linear infinite;
    transform: translate3d(0, 0, 0);
    opacity: 0.8;
    bottom: 0;
    left: 0;
    z-index: -1;
}

.wave:nth-of-type(2) {
    bottom: -1.25em;
    animation: wave 18s linear reverse infinite;
    opacity: 0.8;
}

.wave:nth-of-type(3) {
    bottom: -2.5em;
    animation: wave 20s -1s reverse infinite;
    opacity: 0.9;
}

@keyframes gradient {
    0% {
        background-position: 0% 0%;
    }
    50% {
        background-position: 100% 100%;
    }
    100% {
        background-position: 0% 0%;
    }
}

@keyframes wave {
    2% {
        transform: translateX(1);
    }
    25% {
        transform: translateX(-25%);
    }
    50% {
        transform: translateX(-50%);
    }
    75% {
        transform: translateX(-25%);
    }
    100% {
        transform: translateX(1);
    }
}


/* FOOTER */

footer {
    overflow: hidden;
    color: #fff;
    width: 87.5%;
    margin: auto auto 0;
}

footer a {
    color: #fff;
}

.footer__container {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 0 auto 2vh;
}

.footer__city {
    font-family: 'Chakra Petch';
    font-size: 1.25rem;
}

.hub__lep {
    font-family: 'Lato', sans-serif;
    font-size: 1.45rem;
    user-select: none;
    font-weight: 700;
}

.name__animation {
    animation: glitch 300ms linear;
}

.hub-lep:hover {
    color: transparent;
}

.footer__link {
    user-select: none;
    font-family: 'Cousine', monospace;
    cursor: pointer;
    display: block;
    font-size: 10.5px;
    transition: all .25s cubic-bezier(.175, .885, .32, 1.275);
}

.footer__link span {
    font-size: 14px;
    margin-left: 1rem;
    font-weight: bold;
    font-family: 'Chakra Petch';
}

.footer__link:hover {
    transform: rotateY(12deg) translateZ(0);
}